'use client';

import { Breadcrumb } from "antd";
import Link from "next/link";
import { notFound } from "next/navigation";
import { useParams } from 'next/navigation';

export default function CategoryPage() {
     const params = useParams();
    // return notFound();
    return (
        <div
        style={{
				height: "calc(100%)",
				background:
					"linear-gradient( rgba(208,179,148, 0.4), rgba(208,179,148, 0.7) ), url('/pattern1.png')",
				backgroundRepeat: "repeat",
				backgroundSize: "auto",
				scrollMarginTop: 80
			}}
			className="flex flex-col items-start justify-start p-10 mt-15"
        >
            <Breadcrumb
                items={[
                    {
                        title: <Link className="" href="/">Главная</Link>
                    },
                    {
                        title: <Link className="" href="/#catalogue">Каталог</Link>
                    },
                    {
                        title: <Link className="font-bold" href={`/category/${params.id}`}>{params.id}</Link>
                    }
                ]}
            />
        </div>
    );
}